<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">一、单行文本</p>
            <p>
                之前有介绍过，就不赘述了。点此链接查看：<a target="_blank" href="/#/blog/blog_13">《单行文本溢出隐藏并显示省略号》</a>
            </p>
            <p class="minititle">二、多行文本</p>
            <p>
                （1）直接使用 <span class="tip">CSS</span> 样式
            </p>
            <p>
                使用该方法需要使用 <span class="tip">webkit</span> 内核的浏览器（PC、移动都需要）
            </p>
            <pre class="code"><span class="key">.multi-hide</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">-webkit-line-clamp</span><span class="word">: 3;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">display</span><span class="word">: </span><span class="str">-webkit-box</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">-webkit-box-orient</span><span class="word">: </span><span class="str">block-axis</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">text-overflow</span><span class="word">: </span><span class="str">ellipsis</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">overflow</span><span class="word">: </span><span class="str">hidden</span><span class="word">;</span><br><span class="flower">}</span>
            </pre>
            <p class="multi-hide">
                现在这段话最多只能显示 3 行，超出的内容会被隐藏并显示省略号了。如果你现在使用的是 <span class="tip">webkit</span> 内核的浏览器（比如 Chrome），就可以看到效果了。一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一一
            </p>
            <p>
                如果不考虑兼容性，无疑是多行文本溢出显示省略号最好的方法了。只可惜很多浏览器不兼容这个方法。
            </p>
            <p>
                （2）利用 <span class="tip">CSS</span> 伪类模拟省略号
            </p>
            <pre class="code"><span class="key">.multi-hide_1</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">position</span><span class="word">: </span><span class="str">relative</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">line-height</span><span class="word">: </span><span class="str">1.4em</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">/* 显示几行 height 就设为 line-height 的几倍 */</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">height</span><span class="word">: </span><span class="str">4.2em</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">overflow</span><span class="word">: </span><span class="str">hidden</span><span class="word">;</span><br><span class="flower">}</span><br><span class="key">.multi-hide_1</span><span class="word">::</span><span class="key">after</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">content</span><span class="word">: </span><span class="str">'...'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">font-weight</span><span class="word">: </span><span class="str">bold</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">position</span><span class="word">: </span><span class="str">absolute</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">bottom</span><span class="word">: 0;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">right</span><span class="word">: 0;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">padding</span><span class="word">: </span><span class="str">0 20px 0 44px</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="fun">background</span><span class="word">: </span><span class="str">#fff</span><span class="word">;</span><br><span class="flower">}</span></pre>
            <p>
                这个方法的兼容不错，示例就不贴了。原理是用后面的 <span class="code">...</span> 覆盖了文字的内容，这样的做法从外观上看还是有点不好看。
            </p>
            <p>
                （3）<span class="tip">JS</span> 方案
            </p>
            <p>
                使用 <span class="tip">JS</span> 来控制文本数量显示效果会比较好，当然开销也会大一些。这里有几个插件推荐：<span class="tip">Clamp.js</span>、<span class="tip">jquery.dotdotdot</span>，可以选用。
            </p>
            <p>
                也可以自己写方法处理，之前自己写了一个，但是发现了一些兼容问题，这里就不上代码了，只说说我的思路（这个思路的开销绝对是比较大的）。思路如下：
            </p>
            <p>
                a）设置 <span class="code">line-height</span>（<span class="tip">JS</span> 获取并记为 <span class="code">lineHei</span>）。
            </p>
            <p>
                b）获取指定 DOM 元素的 <span class="code">innerText</span>（记为 <span class="code">str</span>） 和实际的 <span class="code">height</span>（记为 <span class="code">hei</span>），<span class="tip">JS</span> 中设置指定的行数（记为 <span class="code">line</span>）。
            </p>
            <p>
                 c）<span class="code">if (line * lineHei >= hei) </span>，就取 <span class="code">str</span>的前一半，放入指定的 <span class="tip">DOM</span> 元素中，后一半另外保存
            </p>
            <p>
                d）这时候字数如果不能放满 <span class="code">line</span> 行，则把剩下那一半的字从第一个开始一个个加进去，直到超过，将最后两个字替换为 <span class="code">…</span>。
            </p>
            <p>
                这里出现的兼容问题估计是 <span class="code">line * lineHei >= height </span> 出错了。在实际环境中可能受到了 <span class="tip">CSS</span> 框架的影响，而产生了小数点，导致判断出了问题，递归调用上面的方法陷入了死循环。
            </p>
            <p>
                如果能确保 <span class="code">lineHei </span> 和 <span class="code"> hei </span> 准确无误并且不在乎执行的开销的话，可以用上面的思路写写看。（不太推荐）
            </p>
            <p>
                现在的博客列表也有多行省略文字的功能，只不过并没有那么强迫要刚好满几行。而是限制了字数。过滤了标签后，超出 75 字就显示省略号。否则就完整显示。这样的效率很高，而且外观上也还是不错的。
            </p>
        </section>
    </div>
</template>
<script>
import blog from './../../../assets/js/blog'
export default{
  ...blog
}
</script>
<style>
    .multi-hide{
        -webkit-line-clamp: 3;
        display: -webkit-box;
        -webkit-box-orient: block-axis;
        text-overflow: ellipsis;
        overflow: hidden;
}
</style>



